.slot-machine {
  max-width: 728px;
  max-height: 400px;
  margin: 0 20px;
  padding: 0;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  &.fade-in{
    opacity: 1;
  }
  .scroll {
    max-width: 728px;
    max-height: 400px;
    height: 300px;
    overflow: hidden;
    position: relative;
    padding: 0 10px;

    display: flex;
    justify-content: center;
    background: #ffffff;
    border-radius: 40px;
    box-shadow: black 0 0 20px;
    & > div {
      height: 300px;
      display: flex;
      align-items: center;
      &.enter{
        transform: translate(0, 0);
        opacity: 1;
      }
      &.exit{
        transform: translate(0, 100%);
        opacity: 1;
      }
      .slide{
        flex-direction: row !important;
        display: flex;
        align-items: center;
        background: none !important;
        min-height: 300px;
      }
      .slider.animated{
        transition: all .2s linear !important;
      }

      }
      &.selected{
        .slider.animated{
          transition: all .5s ease-in-out !important;
        }
    }
  }

  img {
    width: 100%;
    max-width: 500px;
    max-height: 400px;
  }

}

.blank-slide{
  height: 0;
  width: 0;
}
.shadow{
  position: absolute;
      width: 100%;
      margin: 0 !important;
      pointer-events: none;
      height: 100px !important;
      box-shadow: inset 0px 0px 40px 20px #101010;
      border-radius: 30px;
      border-top-right-radius: 0;
      border-top-left-radius: 0;
      overflow: hidden;
      bottom: 0;
      left: 0;
      background: -webkit-radial-gradient(#00ff0000, #00000073);
      background: radial-gradient(#00ff0000, #00000073);
      background-size: 6px 6px;
}

@media only screen and (min-width: 768px){
  .slot-machine {

    .scroll {

      padding: 0 40px;

      & > div {


        .slide{

        }

        }

    }


  }

}



@media only screen and (min-width: 1200px){
  .slot-machine {
    max-width: 500px;
    max-height: 500px;

    .scroll {
      max-width: 500px;
      max-height: 400px;
      height: 100%;
      overflow: hidden;
      position: relative;
      display: flex;
      justify-content: center;

      & > div {
        height: 400px;
        display: flex;
        align-items: center;

        .slide{
          flex-direction: row !important;
          display: flex;
          align-items: center;
          background: none !important;
          min-height: 400px;
        }


        }

    }


  }

}
