#hero{
  svg{
    transform: translate(0px, -80%);
    margin-bottom: -20%;
    filter: drop-shadow(0px 5px 1px black) drop-shadow(0px 0px 0px #e7008fb5) blur(1px);
    animation: roulette-glow 2s ease-in-out 4s forwards;
    max-width: 800px;
    width: 100%;
    path{
      stroke: #e7008f;
      box-shadow: black 0 0 20px;
    }
    #R1_1_{
      stroke-dasharray: 100;
      stroke-dashoffset: 95;
      animation: r1 .1s ease-in .5s forwards;
    }

    #R2_1_{
      stroke-dasharray: 190;
      stroke-dashoffset: 190;
      animation: r2 .2s ease-in .6s forwards;
    }
    #R3{
      stroke-dasharray: 90;
      stroke-dashoffset: 95;
      animation: r1 .1s ease-in .8s forwards;
    }
    #R4{
      stroke-dasharray: 500;
      stroke-dashoffset: 500;
      animation: r4 .2s ease-in .9s forwards;
    }
    #R5{
      stroke-dasharray: 90;
      stroke-dashoffset: 95;
      animation: r1 .2s ease-in 1.1s forwards;
    }
    #R6{
      stroke-dasharray: 500;
      stroke-dashoffset: 500;
      animation: r4 .5s ease-in 1.1s forwards;
    }
    #O1{
      stroke-dasharray: 190;
      stroke-dashoffset: 190;
      animation: r2 .2s ease-in 1.3s forwards;
    }
    #O2{
      stroke-dasharray: 110;
      stroke-dashoffset: 110;
      animation: o2 .2s ease-in 1.4s forwards;
    }
    #O3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .5s ease-in 1.3s forwards;
    }
    #u1{
      stroke-dasharray: 60;
      stroke-dashoffset: 60;
      stroke-width: 10px;
      animation: u1 .1s ease-in 1.7s forwards;
    }
    #u2{
      stroke-dasharray: 90;
      stroke-dashoffset: 90;
      animation: r1 .2s ease-in 1.8s forwards;
    }
    #u3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .1s ease-in 1.9s forwards;
    }
    #u4{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .1s ease-in 2s forwards;
    }
    #u5{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .1s ease-in 2.1s forwards;
    }
    #u6{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .1s ease-in 2.2s forwards;
    }
    #L1{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .1s ease-in 2.3s forwards;
    }
    #L2{
      stroke-dasharray: 500;
      stroke-dashoffset: 500;
      animation: r4 .3s ease-in 2.5s forwards;
    }
    #L3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .1s ease-in 2.5s forwards;
    }
    #E1{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 2.6s forwards;
    }
    #E2{
      stroke-dasharray: 110;
      stroke-dashoffset: 110;
      animation: o2 .1s ease-in 2.6s forwards;
    }
    #E3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 2.7s forwards;
    }
    #E4{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 2.7s forwards;
    }
    #T1{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 2.8s forwards;
    }
    #T2{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 2.9s forwards;
    }
    #T3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3s forwards;
    }
    #T2_1{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.1s forwards;
    }
    #T2_2{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.2s forwards;
    }
    #T2_3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.3s forwards;
    }
    #E2_1{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.4s forwards;
    }
    #E2_2{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.5s forwards;
    }
    #E2_3{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.6s forwards;
    }
    #E24{
      stroke-dasharray: 330;
      stroke-dashoffset: 330;
      animation: o3 .2s ease-in 3.7s forwards;
    }
    #t_cross_1{
      stroke-width: 5px;
      stroke: #e7008f;
    }
    #t_cross_mask_1_{
      stroke-width: 30px;
      stroke-dasharray: 134;
      stroke-dashoffset: 134;
      animation: t-cross .3s ease-in 3.8s forwards;
    }
  }

}


@keyframes r1 {
  from{
        stroke-dashoffset: 95;
  }
  to{
        stroke-dashoffset: 0;
  }
}

@keyframes r2 {
  from{
        stroke-dashoffset: 190;
  }
  to{
        stroke-dashoffset: 0;
  }
}

@keyframes r4{
  from{
    stroke-dashoffset: 330;
  }
  to{
    stroke-dashoffset: 0;
  }
}

@keyframes o3{
  from{
    stroke-dashoffset: 500;
  }
  to{
    stroke-dashoffset: 0;
  }
}

@keyframes o2{
  from{
    stroke-dashoffset: 110;
  }
  to{
    stroke-dashoffset: 0;
  }
}


@keyframes u1{
  from{
    stroke-dashoffset: 60;
    stroke-width: 10px;
  }
  to{
    stroke-dashoffset: 0;
    stroke-width: 20px;
  }
}

@keyframes t-cross{
  from{
    stroke-dashoffset: 134;
  }
  to{
    stroke-dashoffset: 0;
  }
}

@keyframes roulette-glow{
  from{
      filter: drop-shadow(0px 5px 1px black) drop-shadow(0px 0px 0px #e7008fb5) blur(1px);
  }
  to{
    filter: drop-shadow(0px 5px 1px black) drop-shadow(0px 0px 8px #e7008fb5) blur(1px);
  }
}
